*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body{
  height: 100vh;
  background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}


.App{
  background-color: bisque;
  width: auto;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  border-radius: 10%;
}

h2{
  font-size: 40px;
  text-align: center;
  color: #5cc2a5;
}

form{
  display: flex;
  width: 100%;
  gap: 8px;
  margin: 10px;
  margin-bottom: 12px;
}

form h4{
  font-size: 18px;
}

form input{
  width: 50%;
  background: transparent;
  border: 1px solid #5cc2a5;
  font-size: 15px;
  padding: 6px;
  border-radius: 10px;
}

.to-amount{
  display: flex;
  flex-direction: column;
  align-items: end;
}

form input:hover{
    border: 1px solid white;
}

.from, .to{
  /* background-color: #5cc2a5; */
  padding: 10px;
   width: auto;
   display: flex;
   justify-content: start;
}

.from select, .to select{
  cursor: pointer;
  padding: 8px;
  width: 100%;
  background: transparent;
  border-radius: 10px;
}

.exchange-icon{
  /* background-color: #5cc2a5; */
  cursor: pointer;
  height: auto;
  width : auto;
  padding: 10px;
  font-size: 25px;
  color: #5cc2a5;
  /* transform: rotate(90deg); */
}

.exchange-icon:hover{
  color: white;
  border: 1px solid white;
  border-radius: 10px;
  transition: all 0.3s ease;
}

button{
  cursor: pointer;
  margin: 12px;
  padding :10px;
  font-size: 16px;
  background-color: #5cc2a5;
  color: white;
  border: none;
  border-radius: 16px;
  transition: all 0.3s ease;
}

button:hover{
  border: 1px solid white;
  background-color: bisque;
  color: #5cc2a5;
}

.container{
  /* background-color: blue; */
  display: flex;
  width: 100%;
  justify-content: space-between;
}